import React , { useContext, useEffect } from 'react';
import UsersContext from '../../../context/users/usersContext';
import AuthContext from '../../../context/auth/authContext';

import DashboardGroupItem from './DashboardGroupItem';

const TeacherSubscribedCourses = () => {

  // context
  const usersContext = useContext(UsersContext);
  const authContext = useContext(AuthContext);

  const { groupError, groups, getGroups } = usersContext;
  const { user } = authContext;

  // useEffect
  useEffect(() => {
    if (groups === null)
      getGroups();
    // eslint-disable-next-line
  }, [groups]);

  // Show only the groups where the Teacher is part of 

  const parseCourses = groupList => {
    if (groupList) {
      let parsedGroups = [];
      groupList.map( (group, id) => {

        const {  users } = group;
        // const coursesIds = courses.split(',');
        const isFound = users.find(userObj => userObj.id === user.id);
        if (isFound) {
          // console.log(group);
          parsedGroups.push(<DashboardGroupItem group={group} key={id} />);
          return true;
        }
        return null;
      });
      return parsedGroups;
    }
    return null;
  }
  if (groupError) {
    return (
       <h2>Er is iets misgegaan...</h2>
     )
  } else {

    return (
      <div>
        { parseCourses(groups)}
      </div>
    )
  }
}

export default TeacherSubscribedCourses
