import React, { useContext, useEffect } from 'react';
import CoursesContext from '../../../context/courses/coursesContext';
import DashboardCourseItem from '../DashboardCourseItem';



const DashboardGroupItem = props => {
  const coursesContext = useContext(CoursesContext);
  const { courses , getCourses } = coursesContext;

  const { group } = props;
  let { name , group_id , description } = group.group;
  let descriptionText = "";
  if(description ){
    descriptionText = <section className="group__description"><p>{description}</p></section> ;
    
  }
  useEffect(() => {
    if (!courses) {
      getCourses()
    }
    // eslint-disable-next-line
  }, [courses]);
  
  const findCourses =  inputCourses  => {
    let list = [];
    console.log(courses)
    if( inputCourses  ) {
      inputCourses = inputCourses.split(',');      
      inputCourses.forEach(course => {
        if (courses) {
          courses.find( allCourse =>{
              if(parseInt(allCourse.id) === parseInt(course )){
                  list.push(allCourse);
              }
              return null;
          })
        }
      })
      return list;
    } else {
      return null;
    }
  }

  const foundCourses = findCourses( group.courses );
// console.log(foundCourses)
  if (description)
    descriptionText = <section className="group__description"><p>{description}<br />Aantal studenten: {group.total_users}</p></section>;
  let coursesList = [];
    
  if (foundCourses.length > 0) {
    foundCourses.map( (course , id)  => {
      coursesList.push(<DashboardCourseItem course={course} key={id} />);
      return true;
   })
 }
  return (
    <article className="group__item" id={"group_"+group_id}>

      <header className="dashboard-item-header">
        <h3>{name} </h3>
      </header>
      <article>
        <aside>{descriptionText}</aside>
        <section className="course__grid" >   
          { coursesList}
        </section>
      </article>
  </article>
  )
}

export default DashboardGroupItem
